import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import path from '../../../helpers/path';
import SVG from 'react-inlinesvg';
import config from '../../../config';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './APLogo.css';

const APLogo = props => {
  const { customClass, linkTo } = props;
  const bemClasses = getBEMClasses('ap-logo', customClass);
  const renderSiteLogo = () => {
    if (config.global.siteLogo.match(/.svg$/)) {
      return (
        <SVG
          alt="Site logo"
          src={path(`/local/logos/${config.global.siteLogo}`)}
          className={bemClasses('img')}
        />
      );
    } else {
      return (
        <img
          alt="Site logo"
          src={path(`/local/logos/${config.global.siteLogo}`)}
          className={bemClasses('img')}
        />
      );
    }
  };

  return (
    <div className={bemClasses()}>
      {linkTo ? (
        <Link className={bemClasses('link')} to={path(linkTo)}>
          {renderSiteLogo()}
        </Link>
      ) : (
        renderSiteLogo()
      )}
    </div>
  );
};

APLogo.defaultProps = {
  customClass: '',
  linkTo: ''
};

APLogo.propTypes = {
  customClass: PropTypes.string,
  linkTo: PropTypes.string
};

export default APLogo;



